@import url(https://fonts.googleapis.com/css?family=Raleway:300,300i,400,400i,500,500i,600,600i,700,700i,800,800i);
.App {
  text-align: center;
  font-family: Raleway, sans-serif;
}

.user__name{font-weight:bold;font-size:0.8rem;text-align:right}

.todo{text-align:left;margin:10px;padding:21px 0;background-color:#fff;border-radius:4px;max-width:360px;width:100%;top:0;position:relative;box-shadow:1px 7px 14px -5px rgba(0,0,0,0.2);transition:transform ease-in-out 0.4s;cursor:pointer}.todo:hover{transform:translateY(-5px)}.todo:before{content:"";position:absolute;top:0;left:0;width:4px;height:100%;border-top-left-radius:4px;border-bottom-left-radius:4px}.todo--green:before{background-color:#d980fa}.todo__content{padding:0 60px 0 40px}.todo__status{color:#3e3e3e;font-weight:700;margin-top:0;margin-bottom:8px}.todo__title{font-size:14px;margin-top:0;margin-bottom:0;color:#878787}.todo__close{position:absolute;right:20px;top:45%;width:14px;cursor:pointer}.true{color:green}.false{color:darkred}

.todo__list{display:flex;flex-wrap:wrap;justify-content:center}

.form{display:flex;flex-direction:column;align-items:center}.field{width:200px;border:1px solid gray;border-radius:4px;padding:4px;margin:4px}.error{color:red;font-size:0.7rem;margin:0 0 10px}button.mdl-button{margin:30px;background-color:#d980fa !important}

