.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.field {
  width: 200px;
  border: 1px solid gray;
  border-radius: 4px;
  padding: 4px;
  margin: 4px;

}

.error {
  color: red;
  font-size: 0.7rem;
  margin: 0 0 10px;
}

button.mdl-button {
  margin: 30px;
  background-color: #d980fa !important;
}
